@import './alternative-case-study/card--case-study-alternative';

.card--casestudy {
    a {
        display: inline-block;
        border: $border-width-hairline $border-style-solid $color-gray1;

        @include thumbnail-gradient-scale;
        @include card-shared-properties;
        @include background-color-and-transition;
        width: 100%;

        .card__inner {
            @include card-with-thumbnail;
        }

        &:hover,
        &:active,
        &:focus {
            border: $border-width-hairline $border-style-solid $color-gray1;
        }
    }

    h3 {
        border-bottom: $border-width-default $border-style-solid $color-gray11;
    }
}

.card--casestudy__thumbnail {
    margin-right: $measure--small2;
}
